<template>
  <div id="show-blogs">
      <h1>博客总揽</h1>
      <input type="text" v-model="search" placeholder="搜索">
      <div v-theme:column="'narrow'" v-for="(blog,index) in filteredBlogs" :key="index" class="single-blog">
          <router-link v-bind:to="'/blog/' + blog.id">
                <h2 v-rainbow>
                    {{blog.tittle | to-uppercase}}
                </h2>
          </router-link>
          <article>
              {{blog.content | snippet}}
          </article>
      </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
      return {
          blogs:[],
          search:""
      }
  },
  created(){
      this.$http.get("https://vuedemo-2975e-default-rtdb.firebaseio.com/post.json")
      .then(function(data){
        //   console.log(data.json());
          return data.json();
        //   this.blogs=data.body.slice(0,10);
        //   console.log(this.blogs)
      })
      .then(function(data){
          let blogsArray=[];
          for(let key in data){
            //   console.log(key)
            // console.log(data[key])
            data[key].id =key;
            blogsArray.push(data[key]);
          }
        //   console.log(blogsArray);
          this.blogs = blogsArray;
          console.log(this.blogs);
      })
  },
  computed:{
      filteredBlogs:function(){
          return this.blogs.filter((blog) =>{
              return blog.tittle.match(this.search);
          })
      }
  },
  //本地
  filters:{
      "to-uppercase": function(value){
          return value.toUpperCase();
      },
      "snippet": function(value){
          return value.slice(0,100)+"...";
      }
  },
  directives:{
      "rainbow":{
          bind(el,binding,vnode){
              el.style.color = "#" + Math.random().toString(16).slice(2,8);
          }
      }
  }

}
</script>

<style>
#show-blogs{
    max-width: 800px;
    margin: 0 auto;

}
.single-blog{
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
    border: 1px dotted #aaa;
}
#show-blogs a{
    color: #444;
    text-decoration: none;
}

input[type="text"]{
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
}

</style>



