<template>
    <div id="single-blog">
        <h1>{{blog.tittle}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <p>分类：</p>
        <ul>
            <li v-for="(category,index) in blog.categories" :key="index">
                {{category}}
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "single-blog",
    data(){
        return{
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
        this.$http.get(
            'https://vuedemo-2975e-default-rtdb.firebaseio.com/post/'+this.id + ".json")
            .then(function(data){
                // console.log(data);
            // this.blog = data.body;
                return data.json();
            
        })
        .then(
            function(data){
                this.blog = data;
            }
        )
    }
}
</script>
<style scoped>
 #single-blog{
     max-width: 960px;
     margin: 0 auto;
     padding: 20px;
     background: #eee;
     border: 1px dotted #aaa;
 }

</style>


